@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500;700&display=swap);
*,*:before,*:after{box-sizing:border-box;margin:0;padding:0;font-family:'Roboto', sans-serif}

body{background:#f2f2f2}

table{table-layout:fixed;width:100%;border-collapse:collapse;font-size:20px;font-weight:500}thead{text-align:left}thead th:nth-child(1){width:30%}th,td{padding:15px 20px;border:3px solid #afafaf}.lowest-currency{background:#0091ff;color:white}

.lds-ring-wrapper{position:absolute;left:calc(50% - 32px);top:calc(50% - 32px)}.lds-ring{display:inline-block;position:relative;width:80px;height:80px}.lds-ring div{box-sizing:border-box;display:block;position:absolute;width:64px;height:64px;margin:8px;border:8px solid #afafaf;border-radius:50%;-webkit-animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:#afafaf transparent transparent transparent}.lds-ring div:nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s}.lds-ring div:nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.lds-ring div:nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s}@-webkit-keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

